import "../../node_modules/billboard.js/dist/billboard.css";
import "../styles/barchart.css";
import { bb, bar} from "billboard.js";
import drawLegend from "./components/legend";
import { _ } from "./lang";
import { storage } from "./storage";

const constants = window.configuration.data;
const showExpandButton = window.configuration.showExpandButton;
const yAxisStartFrom = window.configuration.yAxisStartFrom;

// import drawExpandButton from './components/expand';

let chart;

function changeSorting(elem) {
  "use strict";

  jQuery("#nogif").tooltip("hide");

  let data;
  window.orderBy = elem;
  if (constants.sex[0].abbr === elem) {
    data = storage.processedDataMale;
  } else {
    data = storage.processedDataFemale;
  }
  drawBarchart(data[window.active], false, true, window.showFullAxis);
  // if (window.showFullAxis) {
  //     drawBarchart(data[window.active], true, false, true);
  // } else {
  //     drawBarchart(data[window.active], false, false, true);
  // }
}

function changeBars(id) {
  "use strict";

  jQuery("#checkbox-F").tooltip("hide");
  jQuery("#checkbox-M").tooltip("hide");

  if (window.toggledBar === "") {
    window.toggledBar = id;
  } else {
    window.toggledBar = "";
  }

  if (window.orderBy === id) {
    if (constants.sex[0].abbr === id) {
      changeSorting(constants.sex[1].abbr);
    } else {
      changeSorting(constants.sex[0].abbr);
    }
  }
  chart.toggle(id);
  drawLegend(chart, window.toggledBar, window.orderBy);
}

function drawBarchart(data, isNewIndicator, redrawLegend, showFullAxis) {
  "use strict";

  // let minYAxisValue = 0
  let zeroBaseValue = true;
  let yConfig = {
    show: true,
  };

  if (showExpandButton[window.active] && !showFullAxis) {
    zeroBaseValue = true;
    yConfig = {
      show: true,
    };
    yConfig.min = yAxisStartFrom[window.active];
    yConfig.padding = {
      top: 20,
      bottom: 0,
    };
  }

  let padding = 30;

  let h = window.innerHeight * 0.65;
  let w = window.innerWidth;

  let axisRotated = false;

  // create categoris:
  let xCategories = [];

  for (let a = 0, n = data.length; a < n; a += 1) {
    xCategories.push(data[a].country.slice(0, 2)); // cut 27_2020
  }

  // Find matches
  const mql = window.matchMedia("(orientation: portrait)");

  // If there are matches, we're in portrait
  if (mql.matches) {
    // Portrait orientation
    if (w > 321 && w <= 480) {
      axisRotated = true;
      // h = 800;
      h = window.innerHeight * 0.67;
    } else if (w <= 320) {
      axisRotated = true;
      h = window.innerHeight * 0.57;
    }
  } else {
    // Landscape orientation
    // check also for ultrawide screens
    if (window.innerHeight <= 768 && window.innerWidth > 1200) {
      h = window.innerHeight * 0.4;
    } else {
      h = window.innerHeight * 0.53;
    }
  }

  let bindTo = "#barchart";
  w = undefined;

  chart = bb.generate({
    size: {
      height: h - padding,
      width: w,
    },
    bindto: bindTo,
    data: {
      json: data,
      keys: {
        value: ["F", "M"],
      },
      type: bar(),
      colors: {
        F: "rgb(249,165,27)",
        M: "rgb(31,111,184)",
      },
      names: {
        F: _("F"),
        M: _("M"),
      },
    },
    bar: {
      zerobased: zeroBaseValue,
      width: {
        ratio: 0.8,
      },
    },
    legend: {
      show: false,
    },
    tooltip: {
      format: {
        title: function (d) {
          var title = chart.categories(); // .axis.x.categories[d];
          return _(title[d]);
        },
        value: function (value, _ratio, id, index) {
          var title = chart.categories(); // .axis.x.categories[d];
          title = title[index];
          if (title) {
            let valueFormatted = value.toFixed(0);
            valueFormatted = valueFormatted.replace(
              ".",
              _("decimal_separator")
            );

            return valueFormatted + _("tooltip_" + id);
            let tooltip = "";
            if (window.active < 2) {
              tooltip = "tooltip_0_" + id;
            } else {
              tooltip = "tooltip_1_" + id;
            }

            return valueFormatted + _(tooltip);
          }
        },
      },
    },
    grid: {
      y: {
        show: true,
      },
    },
    axis: {
      rotated: axisRotated,
      x: {
        tick: {
          // culling: false,
          fit: true,
          multiline: false,
          // format: '%Y',
          outer: false,
          // culling: {
          //   max: 20
          // }
        },
        type: "category",
        categories: xCategories,
        height: 35,
      },
      y: yConfig,
    },
  });

  // if indicator was changed, we need to apply last status
  if (window.toggledBar !== "" && isNewIndicator) {
    chart.toggle(window.toggledBar);
  }

  if (redrawLegend) {
    drawLegend(chart, window.toggledBar, window.orderBy);
  }
}

export { drawBarchart, changeBars, changeSorting };
