import '../../styles/legend.css'
import { select } from 'd3-selection'
import { _ } from '../lang'
import { changeBars, changeSorting } from '../barchart'

export default function drawLegend (chart, toggledBar, orderBy) {
  document.getElementById('legend-container').innerHTML = '' // empty div before adding

  select('#legend-container')
    .insert('div', '.chart')
    .attr('class', 'legend')
    .selectAll('span')
    .data(['F', 'M'])
    .enter()
    .append('div')
    .attr('data-id', function (id) {
      return id
    })
    .attr('class', 'legend-block')
    .html(function (id) {
      let legendDiv = ''
      if (toggledBar === '') {
        legendDiv += '<div id="checkbox-' + id + '" data-tooltip="tooltip" data-placement="top" title="' + _('hide_' + id) + '">'
        // legendDiv += '<i id="checkbox-' + id + '" class="far fa-check-square fa-custom" data-tooltip="tooltip" data-placement="top" title="' + _('hide_' + id) + '"></i>'
        legendDiv += '<i class="far fa-check-square fa-2x"></i>'
        legendDiv += '</div>'
      } else {
        if (toggledBar === 'F' && id === 'F') {
          // legendDiv += '<i id="checkbox-' + id + '" class="far fa-square fa-custom" data-tooltip="tooltip" data-placement="top" title="' + _('show_' + id) + '"></i>'
          legendDiv += '<div id="checkbox-' + id + '" data-tooltip="tooltip" data-placement="top" title="' + _('show_' + id) + '">'
          legendDiv += '<i class="far fa-square fa-2x"></i>'
          legendDiv += '</div>'
        } else if (toggledBar !== 'F' && id === 'F') {
          legendDiv += '<div class="placeholder"></div>'
        }
        if (toggledBar === 'M' && id === 'M') {
          // legendDiv += '<i id="checkbox-' + id + '" class="far fa-square fa-custom" data-tooltip="tooltip" data-placement="bottom" title="' + _('show_' + id) + '"></i>'
          legendDiv += '<div id="checkbox-' + id + '" data-tooltip="tooltip" data-placement="bottom" title="' + _('show_' + id) + '">'
          legendDiv += '<i class="far fa-square fa-2x"></i>'
          legendDiv += '</div>'
        } else if (toggledBar !== 'M' && id === 'M') {
          legendDiv += '<div class="placeholder"></div>'
        }
      }

      legendDiv += '<div class="box_' + id + '"></div>'
      legendDiv += '<small class="legend-text">' + _(id) + '</small>'
      if (toggledBar === '') {
        if (orderBy === id) {
          // legendDiv += '<i id="darrgif" class="fas fa-caret-down fa-custom unuseable"></i>'
          legendDiv += '<div id="darrgif" class="unuseable">'
          legendDiv += '<i class="fas fa-caret-down fa-2x"></i>'
          legendDiv += '</div>'
        } else {
          // legendDiv += '<i id="nogif" class="fas fa-caret-down fa-custom useable" data-tooltip="tooltip" data-placement="top" title="' + _('sort_' + id) + '"></i>'
          legendDiv += '<div id="nogif" class="useable" data-tooltip="tooltip" data-placement="top" title="' + _('sort_' + id) + '">'
          legendDiv += '<i class="fas fa-caret-down fa-2x"></i>'
          legendDiv += '</div>'
        }
      } else {
        // no sorting
        legendDiv += '<div id="darrgif" class="unuseable">'
        legendDiv += '<i id="darrgif" class="fas fa-caret-down fa-2x"></i>'
        legendDiv += '</div>'
      }
      return legendDiv
    })
    .on('mouseover', function (id) {
      chart.focus(id)
    })
    .on('mouseout', function (id) {
      chart.revert()
    })

  // add click event to checkboxes
  // let checkF = document.getElementById('checkbox-F')
  // if (checkF) {
  //   checkF.addEventListener('click', function () {
  //     changeBars('F')
  //   })
  // }

  select('#checkbox-F')
    .on('click', function (id) {
      changeBars('F')
    })

  select('#checkbox-M')
    .on('click', function (id) {
      changeBars('M')
    })

  // let checkM = document.getElementById('checkbox-M')
  // if (checkM) {
  //   checkM.addEventListener('click', function () {
  //     changeBars('M')
  //   })
  // }
  let nogif = document.getElementById('nogif')
  if (nogif) {
    nogif.addEventListener('click', function () {
      if (orderBy === 'F') {
        changeSorting('M')
      } else {
        changeSorting('F')
      }
    })
  }
  jQuery('[data-tooltip="tooltip"]').tooltip()
}
