import '../../styles/social.css'
import no from './no'
import { _ } from '../lang'
const constants = window.configuration.data

export default function createSocial() {
  'use strict'

  let logoContainer = document.getElementById('logo')
  logoContainer.innerHTML = ''
  let logo = new window.Image()
  logo.src = require('../../images/logoestat.png')
  logo.setAttribute('class', 'img-fluid')
  logoContainer.appendChild(logo)

  // get container
  let socialContainer = document.getElementById('social-container')
  socialContainer.innerHTML = ''

  let currentLocation = window.location
  // always append the lang parameter if not already there
  if (currentLocation.href.indexOf('lang') === -1) {
    currentLocation =
      currentLocation.href +
      (currentLocation.href.indexOf('?') !== -1
        ? '&lang=' + String.locale
        : '?lang=' + String.locale)
  }

  // add info/instructions icon
  let infomark = document.createElement('span')
  infomark.setAttribute('class', 'fa-stack')
  infomark.setAttribute('data-toggle', 'modal')
  infomark.setAttribute('data-target', '#container-modal-instructions')
  infomark.setAttribute('data-tooltip', 'tooltip')
  infomark.setAttribute('data-placement', 'top')
  infomark.setAttribute('title', _('instructions-and-information-header'))

  let circleInfo = document.createElement('i')
  circleInfo.setAttribute('class', 'fas fa-circle fa-stack-2x')

  let iconInfo = document.createElement('i')
  iconInfo.setAttribute('class', 'fas fa-info fa-stack-1x fa-inverse')

  infomark.appendChild(circleInfo)
  infomark.appendChild(iconInfo)

  socialContainer.appendChild(infomark)

  
  if (!window.simple) {
    // only show link to dataset, if outside eurostat's environment
    let parentLocation = window.parent.location
    // check if it is embeded or not
    let isEmbed = window.top !== window.self

    if (
      !parentLocation.hostname.includes('ec.europa.eu') ||
      (parentLocation.hostname.includes('ec.europa.eu') && !isEmbed)
    ) {
      let dataset = document.createElement('span')
      dataset.setAttribute('class', 'fa-stack')
      dataset.setAttribute('data-toggle', 'modal')
      dataset.setAttribute('data-target', '#container-modal-dataset')
      dataset.setAttribute('data-tooltip', 'tooltip')
      dataset.setAttribute('data-placement', 'top')
      dataset.setAttribute('title', _('dataset-icon'))

      let circleData = document.createElement('i')
      circleData.setAttribute('class', 'fas fa-circle fa-stack-2x')

      let iconData = document.createElement('i')
      iconData.setAttribute('class', 'fas fa-table fa-stack-1x fa-inverse')

      dataset.appendChild(circleData)
      dataset.appendChild(iconData)

      socialContainer.appendChild(dataset)

      let langUsed = ''
      if (String.locale === 'de') {
        langUsed = 'DE'
      } else if (String.locale === 'fr') {
        langUsed = 'FR'
      } else {
        langUsed = 'EN'
      }

      let datasetHeader = document.getElementById('dataset-header')
      datasetHeader.innerHTML = ''
      datasetHeader.insertBefore(
        document.createTextNode(_('dataset-header')),
        datasetHeader.firstChild
      )

      let datasetBody = document.getElementById('dataset')
      datasetBody.innerHTML = ''
      let datasetLink0 = document.createElement('a')

      datasetLink0.href = constants.bookmarkLink + '&lang=' + langUsed
      datasetLink0.innerHTML = _('bookmarklink')

      datasetLink0.setAttribute('target', '_blank')
      datasetLink0.setAttribute('rel', 'noopener')
      datasetBody.appendChild(datasetLink0)
      datasetBody.appendChild(document.createElement('br'))

      // datasetLink0.href = constants.bookmarkLink0 + '&lang=' + langUsed
      // datasetLink0.innerHTML = _('bookmarklink_0')

      // datasetLink0.setAttribute('target', '_blank')
      // datasetBody.appendChild(datasetLink0)
      // datasetBody.appendChild(document.createElement('br'))

      // let datasetLink1 = document.createElement('a')

      // datasetLink1.href = constants.bookmarkLink1 + '&lang=' + langUsed
      // datasetLink1.innerHTML = _('bookmarklink_1')

      // datasetLink1.setAttribute('target', '_blank')
      // datasetBody.appendChild(datasetLink1)
      // datasetBody.appendChild(document.createElement('br'))
    }

    if (window.innerWidth > 737) {
      // only show an embed button if we have a device bigger thant 737

      let embed = document.createElement('span')
      embed.setAttribute('class', 'fa-stack')
      embed.setAttribute('data-toggle', 'modal')
      embed.setAttribute('data-target', '#container-modal-embed')
      embed.setAttribute('data-tooltip', 'tooltip')
      embed.setAttribute('data-placement', 'top')
      embed.setAttribute('title', _('embed-icon'))

      let circleEmbed = document.createElement('i')
      circleEmbed.setAttribute('class', 'fa fa-circle fa-stack-2x')
      let iconEmbed = document.createElement('i')
      iconEmbed.setAttribute('class', 'fa fa-code fa-stack-1x fa-inverse')

      embed.appendChild(circleEmbed)
      embed.appendChild(iconEmbed)

      socialContainer.appendChild(embed)
    }

    let twitter = document.createElement('span')
    twitter.setAttribute('class', 'fa-stack')
    twitter.setAttribute('data-tooltip', 'tooltip')
    twitter.setAttribute('data-placement', 'top')
    twitter.setAttribute('title', _('twitter-icon'))

    let circleTwitter = document.createElement('i')
    circleTwitter.setAttribute('class', 'fas fa-circle fa-stack-2x')

    let iconTwitter = document.createElement('i')
    iconTwitter.setAttribute('class', 'fab fa-twitter fa-stack-1x fa-inverse')

    twitter.appendChild(circleTwitter)
    twitter.appendChild(iconTwitter)

    let twitterLink = document.createElement('a')
    twitterLink.appendChild(twitter)
    twitterLink.addEventListener('click', () => {
      // window.open('https://twitter.com/intent/tweet?hashtags=ESTATspotlight&original_referer=http%3A%2F%2Fec.europa.eu%2Feurostat%2Fnews%2Fthemes-in-the-spotlight%2Fgva-employment&ref_src=twsrc%5Etfw&text=GVA%20and%20employment%20by%20sector&tw_p=tweetbutton&url=http%3A%2F%2Fec.europa.eu%2Feurostat%2Fnews%2Fthemes-in-the-spotlight%2Fgva-employment&via=EU_Eurostat', '_blank', 'menubar=no,toolbar=no,resizable=yes,scrollbars=yes,height=600,width=600');
      let twitterText = encodeURIComponent('#Eurostat: ' + _('title'))

      window.open(
        'https://twitter.com/intent/tweet?' + // hashtags=Eurostat
          '&original_referer=' +
          currentLocation +
          '&ref_src=twsrc%5Etfw&text=' +
          twitterText +
          '&tw_p=tweetbutton&' +
          // 'url=http%3A%2F%2Fec.europa.eu%2Feurostat%2Fnews%2Fthemes-in-the-spotlight%2Fgva-employment' +
          'url=' +
          currentLocation +
          '',
        // '&via=EU_Eurostat',
        '_blank',
        'menubar=no,toolbar=no,resizable=yes,scrollbars=yes,height=600,width=600'
      )
      return false
    })

    socialContainer.appendChild(twitterLink)

    let fb = document.createElement('span')
    fb.setAttribute('class', 'fa-stack')
    fb.setAttribute('data-tooltip', 'tooltip')
    fb.setAttribute('data-placement', 'top')
    fb.setAttribute('title', _('facebook-icon'))

    let circleFacebook = document.createElement('i')
    circleFacebook.setAttribute('class', 'fas fa-circle fa-stack-2x')

    let iconFacebook = document.createElement('i')
    iconFacebook.setAttribute('class', 'fab fa-facebook-f fa-stack-1x fa-inverse')

    fb.appendChild(circleFacebook)
    fb.appendChild(iconFacebook)

    let fbLink = document.createElement('a')
    fbLink.appendChild(fb)
    fbLink.addEventListener('click', () => {
      let u = window.location.href
      let t = _('title')
      let url =
        'https://www.facebook.com/sharer/sharer.php?u=' +
        encodeURIComponent(u) +
        '&t=' +
        encodeURIComponent(t)
      window.open(
        url + '?redirect=facebook',
        'sharer',
        'toolbar=0,status=0,width=626,height=436'
      )
    })
    socialContainer.appendChild(fbLink)

    let embedContainer = document.getElementById('embed')
    embedContainer.innerHTML =
      '&lt;iframe width="100%" height="800" src="' +
      currentLocation +
      '"&gt;&lt;/iframe&gt;'

    let embedHeader = document.getElementById('embed-header')
    embedHeader.insertBefore(
      document.createTextNode(_('embed_instruction')),
      embedHeader.firstChild
    )
  } else {
      // no dataset icon, so display standard bookmark footer
      let bookmarkText = document.createTextNode(_('bookmark_link'))
      let datasetLink = document.createElement("a");

      datasetLink.href = constants.bookmarkLink
      datasetLink.innerHTML = _("bookmark_link_key");

      datasetLink.setAttribute("target", "_blank");
      datasetLink.setAttribute("rel", "noopener");

      let datasetBody = document.getElementById("bookmark-link");
      datasetBody.innerHTML = ''
      datasetBody.append(bookmarkText)
      datasetBody.appendChild(datasetLink);
  }

  let instructions = document.getElementById('instructions')
  let instructionsText0 = _('instructions0')
  // let instructionsText1 = _('instructions1');

  let instructionsHeader = document.getElementById('instructions-header')
  instructionsHeader.innerHTML = ''
  let closeButton = document.createElement('button')
  closeButton.setAttribute('type', 'button')
  closeButton.setAttribute('class', 'close')
  closeButton.setAttribute('data-dismiss', 'modal')
  closeButton.setAttribute('aria-label', 'Close')
  closeButton.innerHTML = '<span aria-hidden="true">&times;</span>'

            // <button
              // type="button"
              // class="close"
              // data-dismiss="modal"
              // aria-label="Close"
            // >
              // <span aria-hidden="true">&times;</span>
            // </button>
  // instructionsHeader.appendChild(document.createTextNode(_('instructions-header')));
  instructionsHeader.insertBefore(
    document.createTextNode(_('instructions-header')),
    instructionsHeader.firstChild
  )
  instructionsHeader.appendChild(closeButton)
  // instructionsHeader.appendChild(closeButton);

  instructions.innerHTML = ''
  instructions.appendChild(document.createTextNode(instructionsText0))
  instructions.appendChild(document.createElement('br'))
  // instructions.appendChild(document.createTextNode(instructionsText1));
  // instructions.appendChild(document.createElement('br'));
  instructions.appendChild(document.createTextNode(_('instructions1_part1')))
  instructions.appendChild(no)
  instructions.appendChild(document.createTextNode(_('instructions1_part2')))

  // add informations
  let informationsHeader = document.getElementById('informations-header')
  informationsHeader.innerHTML = ''
  informationsHeader.appendChild(
    document.createTextNode(_('informations-header'))
  )

  let informations = document.getElementById('informations')
  informations.innerHTML = ''
  informations.appendChild(document.createTextNode(_('informations')))
  informations.appendChild(document.createElement('br'))
  informations.appendChild(document.createTextNode(_('footnote_2')))

  // active all tooltips
  $('[data-tooltip="tooltip"]').tooltip()
}
