// const constants = require('../config').constants;
import { storage } from '../storage'
import { drawBarchart } from '../barchart'

import { _ } from '../lang'

const constants = window.configuration.data
const showExpandButton = window.configuration.showExpandButton

function createOnClick (data, isNewIndicator, redrawLegend, showFullAxis) {
  return function () {
    drawBarchart(data, isNewIndicator, redrawLegend, showFullAxis)
    window.showFullAxis = showFullAxis
    toggleExpandName()
  }
}

function toggleExpandName () {
  let showFullAxis = window.showFullAxis
  let buttonAxis = document.getElementById('btn-axis')
  let orderBy = window.orderBy
  let data = orderBy === constants.sex[1].abbr ? storage.processedDataFemale[active] : storage.processedDataMale[active]

  if (showFullAxis) {
    buttonAxis.innerHTML = _('btn_axis_1')
    if (orderBy !== '') {
      buttonAxis.onclick = createOnClick(data, true, true, false)
    } else {
      buttonAxis.onclick = createOnClick(data, false, true, false)
    }
  } else {
    buttonAxis.innerHTML = _('btn_axis_0')
    if (orderBy !== '') {
      buttonAxis.onclick = createOnClick(data, true, true, true)
    } else {
      buttonAxis.onclick = createOnClick(data, false, true, true)
    }
  }
}

export default function drawExpandButton () {
  'use strict'
  let active = window.active
  let buttonAxis = document.getElementById('btn-axis')

  if (showExpandButton[active]) {
    buttonAxis.style.visibility = 'visible'
    toggleExpandName()
  } else {
    buttonAxis.style.visibility = 'hidden'
  }
}
