import 'bootstrap/dist/css/bootstrap.min.css'
import { library, dom } from '@fortawesome/fontawesome-svg-core'
import { faTwitter } from '@fortawesome/free-brands-svg-icons/faTwitter'
import { faFacebookF } from '@fortawesome/free-brands-svg-icons/faFacebookF'
import { faCircle } from '@fortawesome/free-solid-svg-icons/faCircle'
import { faShareAlt } from '@fortawesome/free-solid-svg-icons/faShareAlt'
import { faSpinner } from '@fortawesome/free-solid-svg-icons/faSpinner'
import { faInfo } from '@fortawesome/free-solid-svg-icons/faInfo'
import { faCode } from '@fortawesome/free-solid-svg-icons/faCode'
import { faCheck } from '@fortawesome/free-solid-svg-icons/faCheck'
import { faTable } from '@fortawesome/free-solid-svg-icons/faTable'
import { faCaretDown } from '@fortawesome/free-solid-svg-icons/faCaretDown'
import { faCheckSquare } from '@fortawesome/free-regular-svg-icons/faCheckSquare'
import { faSquare } from '@fortawesome/free-regular-svg-icons/faSquare'
import '../styles/main.css'

import 'bootstrap/js/dist/tooltip'
import 'bootstrap/js/dist/dropdown'
import 'bootstrap/js/dist/modal'
import 'bootstrap/js/dist/collapse'
import createSocial from './components/social'
import { indicode0 } from './components/indicode0'
import { indicode1 } from './components/indicode1'
import { indicode2 } from './components/indicode2'
import { indicode3 } from './components/indicode3'
import { indicode4 } from './components/indicode4'
import { indicode5 } from './components/indicode5'

import { storage } from './storage'
import requestData from './dataservice'
import { _, getParameterValue, localizeFootnoteBarchart } from './lang'
import { drawBarchart } from './barchart'
import drawExpandButton from './components/expand'
import { alignBottomBar } from './components/responsive'

library.add(faTwitter, faFacebookF, faCircle, faShareAlt, faSpinner, faInfo, faCheckSquare, faCheck, faCode, faTable, faSquare, faCaretDown)

function showVis (elem) {
  'use strict'
  const constants = window.configuration.data
  window.active = elem

  jQuery('div').removeClass('active')
  jQuery('#card-' + window.active).addClass('active')

  document.getElementById('subtitle').innerHTML = _('longtitle' + elem + '_0') + '<br>' + _('longtitle' + elem + '_1')
  document.getElementById('shorttitle').innerHTML = _('shorttitle' + elem + '_0') + ' ' + _('shorttitle' + elem + '_1')
  localizeFootnoteBarchart(window.active)

  // document.getElementById('subtitle').innerHTML = _('longtitle' + elem)
  // document.getElementById('shorttitle').innerHTML = _('shorttitle' + elem + '_0') + ' ' + _('shorttitle' + elem + '_1')

  let data
  if (window.orderBy === constants.sex[0].abbr) {
    data = storage.processedDataMale[elem]
  } else {
    data = storage.processedDataFemale[elem]
  }
  drawBarchart(data, true, true, window.showFullAxis)
  drawExpandButton()
  alignBottomBar()
  createSocial()
}

document.addEventListener('touchmove', function (event) {
  event = event.originalEvent || event
  if (event.scale > 1) {
    event.preventDefault()
  }
}, false)

let resizeTimer

$(window).on('resize', function (e) {
  clearTimeout(resizeTimer)
  $('#barchart').css('visibility', 'hidden')
  resizeTimer = setTimeout(function () {
    // Run code here, resizing has "stopped"
    showVis(window.active)
    $('#barchart').css('visibility', 'visible')
  }, 350)
})

document.addEventListener('DOMContentLoaded', () => {
  'use strict'
  dom.watch()

  window.active = window.configuration.active
  window.orderBy = window.configuration.orderBy
  window.toggledBar = window.configuration.toggledBar
  window.showFullAxis = window.configuration.showFullAxis

  const constants = window.configuration.data

  // String.defaultLocale = window.defaultLanguage;
  String.locale = window.defaultLanguage
  String.toLocaleString(window.availableLanguages)
  let lang = getParameterValue('lang')
  if (lang !== '' && lang !== '') {
    for (const value in window.availableLanguages) {
      if (value === lang) {
        String.locale = lang
      }
    }
  }

  // convert vis value from String to int
  const vis = +getParameterValue('vis')
  if (vis && vis >= 0 && vis <= 4) {
    window.active = vis
  }

  const simple = (getParameterValue('simple') === 'true')
  window.simple = simple

  let imageContainer0 = document.getElementById('card-0')
  imageContainer0.insertBefore(indicode0, imageContainer0.firstChild)

  let imageContainer1 = document.getElementById('card-1')
  imageContainer1.insertBefore(indicode1, imageContainer1.firstChild)

  let imageContainer2 = document.getElementById('card-2')
  imageContainer2.insertBefore(indicode2, imageContainer2.firstChild)

  let imageContainer3 = document.getElementById('card-3')
  imageContainer3.insertBefore(indicode3, imageContainer3.firstChild)

  let imageContainer4 = document.getElementById('card-4')
  imageContainer4.insertBefore(indicode4, imageContainer4.firstChild)

  let imageContainer5 = document.getElementById('card-5')
  imageContainer5.insertBefore(indicode5, imageContainer5.firstChild)

  // localise user interface
  document.title = _('document.title')
  let titleWithYear = _('title') + ', ' + constants.year
  document.getElementById('title').innerHTML = titleWithYear

  let shorttitle0 = _('shorttitle0_0') + '<br>' + _('shorttitle0_1')
  document.getElementById('shorttitle0').innerHTML = shorttitle0

  let shorttitle1 = _('shorttitle1_0') + '<br>' + _('shorttitle1_1')
  document.getElementById('shorttitle1').innerHTML = shorttitle1

  let shorttitle2 = _('shorttitle2_0') + '<br>' + _('shorttitle2_1')
  document.getElementById('shorttitle2').innerHTML = shorttitle2

  let shorttitle3 = _('shorttitle3_0') + '<br>' + _('shorttitle3_1')
  document.getElementById('shorttitle3').innerHTML = shorttitle3

  let shorttitle4 = _('shorttitle4_0') + '<br>' + _('shorttitle4_1')
  document.getElementById('shorttitle4').innerHTML = shorttitle4

  let shorttitle5 = _('shorttitle5_0') + '<br>' + _('shorttitle5_1')
  document.getElementById('shorttitle5').innerHTML = shorttitle5

  document.getElementById('card-0').addEventListener('click', () => { showVis(0) })
  document.getElementById('card-1').addEventListener('click', () => { showVis(1) })
  document.getElementById('card-2').addEventListener('click', () => { showVis(2) })
  document.getElementById('card-3').addEventListener('click', () => { showVis(3) })
  document.getElementById('card-4').addEventListener('click', () => { showVis(4) })
  document.getElementById('card-5').addEventListener('click', () => { showVis(5) })

  jQuery.when(requestData()).done(function () {
    jQuery('#loader').css('display', 'none')
    jQuery('#global').css('display', 'block')

    showVis(window.active)

    // add the social media bar
    // createSocial()
  })
})
