function alignBottomBar () {
  'use strict'
  // Find matches
  const mql = window.matchMedia('(orientation: portrait)')

  // If there are matches, we're in portrait
  if (mql.matches) {
    // Portrait orientation
    if (window.innerWidth <= 544) {
      document.getElementById('button-col').className = 'col-sm'
      document.getElementById('legend-col').className = 'col-sm'
      document.getElementById('social-col').className = 'col-sm'

      document.getElementById('social-flex').className = 'd-flex justify-content-center adjust-margin'
      document.getElementById('axis-flex').className = 'd-flex justify-content-center adjust-margin'
      document.getElementById('legend-flex').className = 'd-flex justify-content-center adjust-margin'
    } else if (window.innerWidth <= 768) {
      document.getElementById('button-col').className = 'col-3'
      document.getElementById('legend-col').className = 'col-5 col-auto'
      document.getElementById('social-col').className = 'col-4 col-auto'

      document.getElementById('social-flex').className = 'd-flex justify-content-end adjust-margin'
      document.getElementById('axis-flex').className = 'd-flex justify-content-start adjust-margin'
      document.getElementById('legend-flex').className = 'd-flex justify-content-center adjust-margin'
    } else {
      document.getElementById('button-col').className = 'col'
      document.getElementById('legend-col').className = 'col-6 col-auto'
      document.getElementById('social-col').className = 'col'

      document.getElementById('social-flex').className = 'd-flex justify-content-end'
      document.getElementById('axis-flex').className = 'd-flex justify-content-start'
      document.getElementById('legend-flex').className = 'd-flex justify-content-center'
    }
  } else {
    // landscape
    if (window.innerWidth <= 481) {
      document.getElementById('button-col').className = 'col-sm'
      document.getElementById('legend-col').className = 'col-sm'
      document.getElementById('social-col').className = 'col-sm'

      document.getElementById('social-flex').className = 'd-flex justify-content-center adjust-margin'
      // document.getElementById('axis-flex').className = 'd-flex justify-content-center adjust-margin';
      document.getElementById('legend-flex').className = 'd-flex justify-content-center adjust-margin'
    } else {
      document.getElementById('button-col').className = 'col'
      document.getElementById('legend-col').className = 'col-5 col-auto'
      document.getElementById('social-col').className = 'col'

      document.getElementById('social-flex').className = 'd-flex justify-content-end'
      // document.getElementById('axis-flex').className = 'd-flex justify-content-start';
      document.getElementById('legend-flex').className = 'd-flex justify-content-center'
    }
  }
}

function alignNamedButtons (amount) {
  'use strict'

  jQuery('button').removeClass('active')
  let w = window.innerWidth

  // let h = window.innerHeight

  // Find matches
  const mql = window.matchMedia('(orientation: portrait)')

  for (let a = 0, n = amount; a < n; a += 1) {
    let small = 'btn btn-secondary btn-sm'

    let large = 'btn btn-secondary btn-lg'

    if (window.active === a) {
      small = small + ' active'
      large = large + ' active'
    }

    // If there are matches, we're in portrait
    if (mql.matches) {
      // Portrait orientation
      // if ((w <= 544 && w < h) ||  (w <= 568 && w > h)) {
      if (w <= 568) {
        document.getElementById('button-' + a).className = small
        document.getElementById('button-flex').className = 'col'
      } else {
        document.getElementById('button-' + a).className = large
        // document.getElementById('button-flex').className = 'col col-md-4 offset-md-4';
      }
    } else {
      // Landscape orientation
      if (w <= 768) {
        document.getElementById('button-' + a).className = small
        document.getElementById('button-flex').className = 'col'
      } else {
        document.getElementById('button-' + a).className = large
      }
    }
  }
}

